import { GetServerSideProps } from "next";
import axios from "axios";
import { useAppDispatch } from "@/hooks/reduxHooks";
import { login } from "@/redux/authSlice";
import { useState } from "react";

const AccountPage = ({ userData }: { userData: any }) => {
  const dispatch = useAppDispatch();
  const [username, setUsername] = useState<string>(userData?.username || "");
  const [isEditing, setIsEditing] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(false);

  // Збереження змін користувача
  const handleSave = async () => {
    try {
      setIsLoading(true);
      const res = await axios.put("http://localhost:5200/api/auth/update-profile", {
        username,
      });
      dispatch(login(res.data.user)); // оновлюємо Redux після збереження
      setIsEditing(false);
    } catch (err) {
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="border-borderColor border-[1px] rounded-lg p-6 flex flex-col">
      <div className="mb-5 flex flex-col gap-2">
        <h1 className="leading-7 text-2xl font-semibold">Account Settings</h1>
        <p className="text-gray leading-5">Manage your personal information and password.</p>
      </div>

      {isLoading ? (
        <div className="animate-spin rounded-full h-12 w-12 border-t-4 border-blue-500 border-solid mx-auto"></div>
      ) : (
        <form>
          <div className="grid grid-cols-2 gap-x-4">
            <div className="flex flex-col gap-[10px]">
              <label className="leading-3.5 font-medium">Full Name</label>
              <input
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                className="input"
                type="text"
                disabled={!isEditing}
              />
            </div>
          </div>

          <div className="flex gap-4 mt-4">
            {isEditing ? (
              <button
                type="button"
                onClick={handleSave}
                className="btn p-2 bg-blue-500 text-white rounded"
              >
                Save Changes
              </button>
            ) : (
              <button
                type="button"
                onClick={() => setIsEditing(true)}
                className="btn p-2"
              >
                Edit
              </button>
            )}
          </div>
        </form>
      )}
    </div>
  );
};

// Отримуємо профіль користувача на сервері
export const getServerSideProps: GetServerSideProps = async (context) => {
  try {
    const res = await axios.get("http://localhost:5200/api/auth/profile", {
      withCredentials: true, // Для забезпечення авторизації
    });
    const userData = res.data.user;

    return {
      props: {
        userData,
      },
    };
  } catch (err) {
    console.error("Error fetching user data:", err);
    return {
      props: {
        userData: null,
      },
    };
  }
};

export default AccountPage;
